.privacy-item {
  margin-bottom: 3rem;
}
.FAQ-question {
  border-bottom: 2px solid #000;

  padding-bottom: 5px;
}
.footer-logo{
  background-color:#1B143C;
}

.FAQ-answer {
 

  letter-spacing: 2px;
  font-weight:400;
}
.privacy-list-head {
  margin: 1rem 0;
  //   font-size: 150%;
  font-weight: 100;
  border-left: 7px solid #0837d5;
  padding: 0.6em 1.5em;
  margin: 30px 0;
}
.sub-head-list {
  list-style-type: decimal;
  margin-top: 1rem;
}
.sub-head-title {
  border-bottom: 1px solid;
  margin-top: 1rem;
  padding-bottom: 10px;
  font-weight: 600;
}
@media only screen and (max-width: 1024px) {
  .privacy-list-head {
    font-size: 20px;
    padding: 0 1.5em;
  }
}
@media only screen and (max-width: 767px) {
  .faq-container{
    padding: 10px!important;
  }
}

.ques-container {
  background-color: #faf8fb;
  font-weight: 600;
  color: #6e6c6f;
  border-left: 5px solid #c1223a;
  position: relative;
  padding-left: 3.5rem;
  padding-top: 20px;
  padding-bottom: 20px;
  margin: 3px 0;
  cursor: pointer;
  .faq-txt {
    position: absolute;
    left: 25px;
    font-size: 3rem;
    top: 40%;
    transform: translate(-50%, -50%);
  }
  .faq-ques-txt {
    color: #c1223a;
  }
}

.ans-container {
  color: #bdbdbd;
  border-left: 5px solid #a5a5a5;
  position: relative;
  padding-left: 3.5rem;
  padding-top: 20px;
  padding-bottom: 20px;
  margin: 3px 0;
  .faq-txt {
    position: absolute;
    left: 25px;
    font-size: 3rem;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  .faq-ques-txt {
    color: #c1223a;
  }
}

.block-item {
  margin: 1em 0;
  padding: 0.75em 1.5em 1em;
  background: #eee;
  color: #000;
}
.block-head {
  font-weight: 600;
  margin-bottom: 0.1rem;
}
