.privacy-item {
  margin-bottom: 3rem;
}
.privacy-list-head {
  margin: 1rem 0;
  //   font-size: 150%;
  font-weight: 100;
  border-left: 7px solid #0837d5;
  padding: 0.6em 1.5em;
  margin: 30px 0;
}
.sub-head-list {
  list-style-type: decimal;
  margin-top: 1rem;
}
.sub-head-title {
  border-bottom: 1px solid;
  margin-top: 1rem;
  padding-bottom: 10px;
  font-weight: 600;
}
.privacy-sub-header{
  font-size: 1.3rem;
}
.privacy-wrap{
  font-size: 1.25rem;
}
@media only screen and (max-width: 1024px) {
  .privacy-list-head {
    font-size: 20px;
    padding: 0 1.5em;
  }
}
@media only screen and (max-width: 767px) {
  .privacy-wrap{
    font-size: 1.0rem!important;
  }
  .privacy-sub-header{
    font-size: 1.1rem!important;
  }
}
